/**
 * @explain
 * This storybook decorator is used to add the base headline text style to the
 * story to make it look like an article headline.
 */
import { FC } from 'react';
import { styled } from 'styled-components';

import { tablet, laptop } from '#theme/devices';

const Wrapper = styled.div`
  font: ${({ theme }) => theme.textStyle.headline.base.mobile};

  @media ${tablet} {
    font: ${({ theme }) => theme.textStyle.headline.base.tablet};
  }

  @media ${laptop} {
    font: ${({ theme }) => theme.textStyle.headline.base.laptop};
  }
`;

const Headline = (Story: FC) => (
  <Wrapper>
    <Story />
  </Wrapper>
);

export default Headline;
